import { FC, RefObject } from "react"
import { User, UserData } from "./User"

interface Props {
  users: UserData[]
}

export const UserList: FC<Props> = ({ users }) => {
  const setLastMessageElement = (user: UserData, ref: RefObject<HTMLDivElement>) => {
    const lastUser = users[users.length - 1]
    if (user.id === lastUser.id) {
      ref && ref.current && ref.current.scrollIntoView({ behavior: "smooth" })
    }
  }

  return (
    <div>
      {users.map((user) => (
        <User user={user} key={user.id} setRef={setLastMessageElement} />
      ))}
    </div>
  )
}
