import { Octokit, RestEndpointMethodTypes } from "@octokit/rest"
import { env } from "process"

const token = env.GITHUB_ACCESS_TOKEN || null
const octokit = new Octokit({ auth: token })

export default class Github {
  // public async searchUsers(targetUser: string): Promise<OctokitResponse<any>> {
  public async searchUsers(targetUser: string): Promise<RestEndpointMethodTypes["search"]["users"]["response"]> {
    // see: https://docs.github.com/ja/search-github/searching-on-github/searching-users
    const queries = [targetUser, "in:login", "repos:>5"]
    return await octokit.rest.search.users({ q: queries.join("+") })
  }

  public async existUser(targetUser: string): Promise<boolean> {
    const queries = [targetUser, "in:login"]
    const users = await octokit.rest.search.users({ q: queries.join("+") })
    let found = false
    users.data.items.map((item) => {
      found = item.login == targetUser || found
    })
    return found
  }

  public async getUser(targetUser: string): Promise<RestEndpointMethodTypes["users"]["getByUsername"]["response"]> {
    return await octokit.rest.users.getByUsername({ username: targetUser })
  }
}
