import React, { FC } from "react"

interface Props {
  name: string
  value: string
  placeholder: string
  onChange: (event: React.ChangeEvent<HTMLInputElement>) => void
}

export const Input: FC<Props> = (props) => {
  const { value, onChange, name } = props
  return (
    <>
      <input
        placeholder={props.placeholder}
        type="text"
        name={name}
        value={value}
        onChange={(e) => onChange(e)}
        minLength={3}
        pattern="^[a-zA-Z0-9]+$"
      />
    </>
  )
}
