import { FC, RefObject, useEffect, useRef } from "react"
import { ProfileCard } from "./profile/ProfileCard"

interface Props {
  user: UserData
  setRef: (user: UserData, ref: RefObject<HTMLDivElement>) => void
}

export type UserData = {
  id: number
  login: string
  name: string
  github_url: string
  avatar_url: string
  resource_username: string
  followers: number
  following: number
  public_repos: number
}

export const User: FC<Props> = ({ user, setRef }) => {
  // see: https://qiita.com/takano-h/items/7f2af3ea2542233ca5cd
  const ref = useRef<HTMLDivElement>(null)

  // ref  setRef  ref 
  useEffect(() => {
    setRef(user, ref)
  }, [setRef, user, ref])

  return (
    <div ref={ref}>
      {user.resource_username.length > 0 ? (
        <p>
          {user.resource_username}  {user.login}
        </p>
      ) : (
        <p>{user.login}</p>
      )}
      <ProfileCard
        login={user.login}
        name={user.name}
        github_url={user.github_url}
        avatar_url={user.avatar_url}
        followers={user.followers}
        following={user.following}
        repos={user.public_repos}
      />
    </div>
  )
}
