import { FC } from "react"
import "./ProfileCard.css"

type ProfileCardProps = {
  login: string
  name: string
  github_url: string
  avatar_url: string
  followers: number
  following: number
  repos: number
}

export const ProfileCard: FC<ProfileCardProps> = (props) => {
  return (
    <div className="card-container">
      <a href={props.github_url}></a>
      <header>
        <img src={props.avatar_url} alt={props.login} />
      </header>
      <h1 className="bold-text">{props.login}</h1>
      <h2 className="normal-text">{props.name}</h2>
      <div className="social-container">
        <div className="followers">
          <h1 className="bold-text">{props.followers}</h1>
          <h2 className="smaller-text">Followers</h2>
        </div>
        <div className="likes">
          <h1 className="bold-text">{props.following}</h1>
          <h2 className="smaller-text">Following</h2>
        </div>
        <div className="photos">
          <h1 className="bold-text">{props.repos}</h1>
          <h2 className="smaller-text">Repos</h2>
        </div>
      </div>
    </div>
  )
}
